    height: 0.44rem;
    background: url("../imgs/three_img_bgbgbg.png");
    background-size: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -0.22rem;
    margin-top: -0.22rem;
}
.salerecord-list li .text {
    position: absolute;
    width: 3rem;
    left: 55%;
    top: 30%;
//  top: 0;
//  bottom: 0;
//  margin: auto;
//  height: 1rem;
    transform: translateY(-50%);
}

/*偶数行文字位置*/
.salerecord-list li:nth-child(even) .text {
//  text-align: right;
//  left: 5.5%;
}
.salerecord-list .picihao .text {
    text-align: right;
    left: 5.5%;
}
.salerecord-list li .text p {
    margin-bottom: 0.1rem;
    /*width: 3rem;*/
    /*font-size: 0.2rem;*/
}
.salerecord-list li .text .span {
    font-size: 0.2rem;
    color: #D1E4F5;
  	margin-bottom: 0.1rem;
